import Api from "../services/Api";
import axios from "axios";
import { observable, computed } from "mobx";

class AuthStore {
  private readonly LOCAL_STORAGE_TOKEN = "Yt-Token";
  private readonly LOCAL_STORAGE_REFRESH_TOKEN = "Yt-Refresh-Token";

  @observable user: any = null; // user
  @observable userInfo: any = {}; // website
  token: string | null = null;
  responseLoginPromise: Promise<any> | null = null;
  api: Api;

  constructor(api: Api) {
    this.api = api;
    this.init();
    this.getFetchUser();
  }

  private getFetchUser = async () => {
    try {
      const { data } = await this.api.getCurrentUser();
      this.storeUser(data.user);
    } catch (error) {}
  };

  get website() {
    return this.user?.website;
  }

  private init = () => {
    axios.interceptors.request.use(
      (config) => {
        if (config.url?.includes("api") && this.getToken()) {
          config.headers = { ...config.headers, ...this.getAuthHeaders() };
        }
        return config;
      },
      (error) => {
        return Promise.reject(error);
      }
    );

    // Add a response interceptor
    axios.interceptors.response.use(
      (response) => {
        if (response.data.token) {
          this.storeToken(response.data.token);
        }
        if (response.data.user) {
          this.storeUser(response.data.user);
        }
        return response;
      },
      async (error) => {
        if (error.response.status !== 401) {
          return Promise.reject(error);
        }

        if (this.responseLoginPromise === null) {
          this.clear();
        } else {
          // waiting for return refresh login/token
          await this.responseLoginPromise;
          this.responseLoginPromise = null;
          // when request for refresh login/token return with 401
          if (this.getToken() === null) {
            // return source error
            return Promise.reject(error);
          }
        }

        if (error.config.url.includes("login")) {
          return Promise.reject(error);
        }

        if (this.getToken() === null) {
          try {
            const responseLoginPromise: any = this.login();
            this.responseLoginPromise = responseLoginPromise;
            const response: any = await responseLoginPromise;
            this.responseLoginPromise = null;
            this.storeToken(response.data.token);
            this.storeUser(response.data.website); // website is user
          } catch (error) {
            return Promise.reject(error);
          }
        }

        try {
          return await axios.request(error.config);
        } catch (error) {
          return error;
        }
      }
    );
  };

  // private login = async () => {
  //   try {
  //     const response = await this.api.login();
  //     return response;
  //   } catch (error) {
  //     return error;
  //   }
  // };

  @computed get isAuthenticated(): boolean {
    return this.user !== null;
  }

  getAuthHeaders() {
    return { Authorization: "Bearer " + this.getToken() };
  }

  storeUser(user: any): void {
    this.user = user;
  }

  storeToken(token: string): void {
    localStorage.setItem(this.LOCAL_STORAGE_TOKEN, token);
  }

  storeRefreshToken(refreshToken: string): void {
    localStorage.setItem(this.LOCAL_STORAGE_REFRESH_TOKEN, refreshToken);
  }

  clear(): void {
    localStorage.removeItem(this.LOCAL_STORAGE_TOKEN);
    localStorage.removeItem(this.LOCAL_STORAGE_REFRESH_TOKEN);
  }

  private getRefreshToken(): string | null {
    return localStorage.getItem(this.LOCAL_STORAGE_REFRESH_TOKEN);
  }

  private getToken(): string | null {
    return localStorage.getItem(this.LOCAL_STORAGE_TOKEN);
  }

  // ******************************************************

  login = async () => {
    return await this.api.login(this.userInfo);
  };

  register = async () => {
    return await this.api.register(this.userInfo);
  };

  resetPassword = async () => {
    try {
      console.log(this.userInfo);
      const { data } = await this.api.register(this.userInfo);
      return data;
    } catch (error) {
      return error;
    }
  };
}

export default AuthStore;
