import Api from "../services/Api";
import { runInAction, observable, computed, action } from "mobx";

class CampaignsStore {
  api: Api;
  total: number = 0;

  // hold active campaigns
  activeIds: number[] = [];

  // hold all user campaign in database
  @observable campaigns: any = [];

  constructor(api: Api) {
    this.api = api;
  }

  @computed get activeCampaigns() {
    return this.campaigns.filter((campaign: any) => campaign.active === 1);
  }

  fetch = async () => {
    const response = await this.api.fetchCampaigns();
    if (response.status === 200) {
      this.campaigns = response.data;
      this.activeIds = this.activeCampaigns.map((campaign: any) => campaign.id);
    }
    return this.campaigns;
  };

  @action onCampaignChange = async (id: string, changes: any) => {
    if (this.activeCampaigns.length === 3 && changes.active === 1) {
      return;
    }
    let campaign = this.campaigns.find(
      (campaign: any) => campaign.id === Number(id)
    );
    const campaignCopy = { ...campaign, ...changes };

    await this.api.updateCampaign(campaignCopy);
    Object.keys(changes).forEach(
      (key: any) =>
        typeof campaign[key] !== "undefined" && (campaign[key] = changes[key])
    );
  };

  fetchTotal = async (startTime: number, endTime: number) => {
    const response = await this.api.fetchTotalCampaigns(startTime, endTime);
    if (response.status === 200) {
      runInAction(() => {
        this.total = response.data.total;
      });
    }
  };
}

export default CampaignsStore;
