import { observable, runInAction } from "mobx";

export type fetchPageResponse = {
  total: number;
  items: any[];
};
abstract class PageStore {
  readonly DEFAULT_PAGE_SIZE = 30;
  readonly pageSizeOptions: any = ["10", "30", "40", "100"];

  @observable pageSize: number = this.DEFAULT_PAGE_SIZE;
  /**
   * Total items
   */
  @observable total: number = 0;
  @observable currentPageNumber: number = 1;
  @observable currentPage: any = [];
  @observable pages: { [key: string]: any } = {};

  constructor() {}

  async fetchPage({
    page = 1,
    pageSize = this.DEFAULT_PAGE_SIZE,
    startTime,
    endTime,
  }: {
    page?: number;
    pageSize?: number;
    startTime?: number | undefined;
    endTime?: number | undefined;
  }) {
    // pageSize = Math.max(this.DEFAULT_PAGE_SIZE, pageSize);
    pageSize = Math.max(0, pageSize);
    page = Math.max(1, page);

    const offset = Math.max(0, (page - 1) * pageSize - 1);
    const limit = pageSize;
    const response: any = await this._fetchPage(
      offset,
      limit,
      startTime,
      endTime
    );
    if (response.status === 200) {
      runInAction(() => {
        this.pages[page] = response.data.traffixs;
        this.currentPage = this.pages[page];
        this.total = response.data.total;
        this.currentPageNumber = page;
        this.pageSize = pageSize;
      });
    }
  }

  abstract _fetchPage(
    offset: number,
    limit: number,
    startTime?: number | undefined,
    endTime?: number | undefined
  ): Promise<fetchPageResponse>;
}

export default PageStore;
