import React from 'react';
import { withRouter, RouteComponentProps, Link } from 'react-router-dom';
import { RegisterStyled } from './styled';
import { Button, Input } from 'antd';
import AuthFooter from '../components/AuthFooter/AuthFooter';
import AuthStore from '../../../stores/AuthStore';
import { inject, observer } from 'mobx-react';
import axios from 'axios';
import Api from '../../../services/Api';
import { observable } from 'mobx';

interface Props extends RouteComponentProps<any> {
	authStore?: AuthStore;
	api?: Api;
}

@inject('authStore', 'api')
@observer
class Register extends React.PureComponent<Props, any> {
	@observable error: any = '';

	userData: any = {
		url: '',
		email: '',
		user_info: {},
		site_info: {},
		campaigns: []
	};

	componentDidMount = async () => {
		this.fetchUserInfo();
		this.fetchSiteInfo();
	};

	fetchUserInfo = async () => {
		try {
			const response = await axios.get('https://api.userinfo.io/userinfos', {
				withCredentials: true,
				headers: { 'X-Userinfo-Client-Id': 'userinfo-js:1.1.2-SNAPSHOT' }
			});

			this.handleUserData({ user_info: response.data });
		} catch (err) {
			console.error(err);
		}
	};

	fetchSiteInfo = async () => {
		const response = await this.props!.api!.getUserData();
		if (response.data) {
			this.handleUserData({
				site_info: response.data,
				...response.data
			});
		}
	};

	handleUserData = (data: any) => {
		// this.userData = { ...this.userData, ...data };
		this.props.authStore!.userInfo = {
			...this.props.authStore!.userInfo,
			...data
		};
	};

	register = async () => {
		try {
			const { data, status } = await this.props.authStore!.register();
		} catch ({ response: { status, data } }) {
			if (status === 400) {
				this.error = data.message;
			}
		}
	};
	render() {
		return (
			<RegisterStyled>
				<div className="auth_container auth_container_small">
					<div>
						{/* <LoadingBar
              progress={FormStore.loadingBarProgress}
              height={3}
              divor="#7473fd"
              onLoaderFinished={() => FormStore.onLoaderFinished()}
            /> */}

						<div className=" textCenter">
							<div>
								<div className="f18  marginHor10">Create YourTraffix Account</div>
							</div>
						</div>
						<div className="marginHor20">
							<Input
								onChange={(e) => (this.props.authStore!.userInfo!.username = e.target.value)}
								className="marginTop10"
								type={'email'}
								placeholder={'Email'}
							/>
							<Input
								onChange={(e) => (this.props.authStore!.userInfo!.password = e.target.value)}
								className="marginTop10"
								type={'password'}
								placeholder={'Password'}
							/>

							{this.error && <div className="red">{this.error}</div>}

							<Button
								onClick={this.register}
								type="primary"
								size={'large'}
								className="b-yellow  br-yellow full marginTop20"
							>
								Register
							</Button>

							<div>
								<div className="f12 textCenter marginTop20 ">Join Our Social Advertising Network</div>
							</div>

							<div className="grid marginTop20">
								<Link to="/users/forgot-password" className="full gray2 f12">
									Forgot Password?
								</Link>
								<Link to="/users/login" className="full gray2 f12">
									Return to login
								</Link>
							</div>

							<AuthFooter />
						</div>
					</div>
				</div>
			</RegisterStyled>
		);
	}
}
export default withRouter(Register);
