import React from 'react';
import { BrowserRouter } from 'react-router-dom';
import { Provider } from 'mobx-react';
import LeftMenu from './common/LeftMenu/LeftMenu';
import AppRouter from './pages/AppRouter';
import './App.scss';
import TraffixsStore from './stores/TraffixsStore';
import DashboardStore from './stores/DashboardStore';
import Api from './services/Api';
import PageHeader from './common/PageHeader/PageHeader';
import CampaignsStore from './stores/CampaignsStore';
import AuthStore from './stores/AuthStore';

const api = new Api();
const campaignsStore = new CampaignsStore(api);
const traffixsStore = new TraffixsStore(api);
const authStore = new AuthStore(api);

const stores = {
	api,
	campaignsStore,
	traffixsStore,
	authStore,
	dashboardStore: new DashboardStore(api, campaignsStore, traffixsStore)
};

function App() {
	return (
		<Provider {...stores}>
			<BrowserRouter>
				<div className="App">
					<div>
						<PageHeader />
					</div>
					<div className="flex">
						<LeftMenu />
						<div className="main_content">
							<AppRouter />
						</div>
					</div>
				</div>
			</BrowserRouter>
		</Provider>
	);
}

export default App;
