import React from "react";
import {
  withRouter,
  RouteComponentProps,
  Link,
  Redirect,
} from "react-router-dom";
import IntroSteps from "./IntroSteps/IntroSteps";
import "./Intro.scss";
import { observable, action } from "mobx";
import { observer, inject } from "mobx-react";
import StepsRoute from "./Steps/StepsRoute";
import { Button } from "antd";
import axios from "axios";
import Api, { ILogin } from "../../services/Api";
import AuthStore from "../../stores/AuthStore";

interface Props extends RouteComponentProps<any> {
  api?: Api;
  authStore?: AuthStore;
}

@inject("api", "authStore")
@observer
class Intro extends React.PureComponent<Props, any> {
  @observable currentStep: any = 0;

  @observable userData: ILogin = {
    url: "",
    email: "",
    user_info: {},
    site_info: {},
    campaigns: [],
  };

  componentDidMount = async () => {
    this.fetchUserInfo();
    this.fetchSiteInfo();
  };

  /**
   * fetch to wordpress - get any info website
   */
  fetchSiteInfo = async () => {
    const response = await this.props!.api!.getUserData();
    if (response.data) {
      this.handleUserData({
        site_info: response.data,
        ...response.data,
      });
    }
  };

  fetchUserInfo = async () => {
    try {
      const response = await axios.get("https://api.userinfo.io/userinfos", {
        withCredentials: true,
        headers: { "X-Userinfo-Client-Id": "userinfo-js:1.1.2-SNAPSHOT" },
      });

      this.handleUserData({ user_info: response.data });
    } catch (err) {
      console.error(err);
    }
  };

  changeStep = (step: any) => {
    if (step === 2 && this.userData.campaigns.length < 1) {
      return;
    }
    this.currentStep = step;
  };

  handleUserData = (data: any) => {
    this.userData = { ...this.userData, ...data };
  };

  @action signUp = async () => {
    try {
      const response: any = await this.props!.api!.setWebsiteEnabled(
        this.props!.authStore!.website,
        this.userData.campaigns
      );
      if (this.props.authStore) {
        this.props.authStore.user.website.is_enabled = 1;
      }
    } catch (error) {}
  };

  render() {
    return this.props.authStore!.user.website.is_enabled ? (
      <Redirect to="/dashboard" />
    ) : (
      <div className="Intro textLeft page-body padding30">
        <IntroSteps currentStep={this.currentStep} />
        <StepsRoute
          step={this.currentStep}
          handleUserData={this.handleUserData}
        />
        <div className="marginTop20 textCenter">
          {this.currentStep === 1 ? (
            <Button
              onClick={this.signUp}
              className="b-yellow br-yellow"
              disabled={this.userData.campaigns.length === 0}
            >
              Enable Your Traffix
            </Button>
          ) : (
            <>
              {this.currentStep === 1 ? (
                <Button onClick={() => this.changeStep(this.currentStep - 1)}>
                  Prev
                </Button>
              ) : null}
              {this.currentStep === 2 ? (
                <Button
                  onClick={() => this.changeStep(this.currentStep + 1)}
                  className="b-yellow br-yellow"
                >
                  <Link to="/dashboard">Dashboard</Link>
                </Button>
              ) : (
                <Button
                  onClick={() => this.changeStep(this.currentStep + 1)}
                  className="b-yellow br-yellow"
                >
                  Next
                </Button>
              )}
            </>
          )}
        </div>
      </div>
    );
  }
}
export default withRouter(Intro);
