import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import {
  withScriptjs,
  withGoogleMap,
  GoogleMap,
  Marker,
} from "react-google-maps";
import { exampleMapStyles } from "./MapStyle";
import "./TraffixMap.scss";
import { observer } from "mobx-react";

const API_KEY = "AIzaSyDrlg1B4lAwQzP7mvE0zt387eQE7wfdW1M";
let lat: string;
let lng: string;

const MapWithAMarker = withScriptjs(
  withGoogleMap((props: any) => (
    <GoogleMap
      defaultOptions={{
        scrollwheel: true,
        draggable: true,
        styles: exampleMapStyles,
        streetViewControl: false,
        scaleControl: false,
        mapTypeControl: false,
        panControl: false,
        zoomControl: false,
        rotateControl: false,
        fullscreenControl: false,
      }}
      defaultZoom={3}
      defaultCenter={{
        lat: 31.8675032,
        lng: 35.2697794,
      }}
    >
      {props.traffix.map((marker: any, i: number) => (
        <Marker
          position={{
            lat: Number(marker.visitor_info?.position?.latitude),
            lng: Number(marker.visitor_info?.position?.longitude),
          }}
          key={i}
        ></Marker>
      ))}
    </GoogleMap>
  ))
);

interface Props extends RouteComponentProps<any> {
  traffix: any;
}

@observer
class TraffixMap extends React.PureComponent<Props, any> {
  render() {
    return (
      <MapWithAMarker
        googleMapURL={`https://maps.googleapis.com/maps/api/js?key=${API_KEY}&libraries=geometry,drawing,places`}
        loadingElement={<div style={{ height: `100%` }} />}
        containerElement={<div style={{ height: "100%" }} />}
        mapElement={<div style={{ height: `100%` }} />}
        traffix={this.props.traffix}
      />
    );
  }
}
export default withRouter(TraffixMap);
