import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import { FlagsStyled } from "./styled";

interface Props extends RouteComponentProps<any> {
  countryCode: string;
  width?: number;
  height?: number;
  style?: any;
}

class Flags extends React.PureComponent<Props, any> {
  render() {
    return (
      <FlagsStyled
        src={`/flags/${this.props?.countryCode?.toLowerCase()}.svg`}
        style={this.props.style || {}}
      />
    );
  }
}
export default withRouter(Flags);
