import React from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import './Campaigns.css';
import PageTitle from '../../common/PageTitle/PageTitle';
import { Select } from 'antd';
import { ReactSVG } from 'react-svg';
import { observer, inject } from 'mobx-react';
import CampaignsStore from '../../stores/CampaignsStore';
const { Option } = Select;

interface Props extends RouteComponentProps<any> {
	campaignsStore: CampaignsStore;
}

@inject('campaignsStore')
@observer
class Campaigns extends React.PureComponent<Props, any> {
	componentDidMount = async () => {
		try {
			await this.props.campaignsStore.fetch();
		} catch (err) {}
	};
	render() {
		return (
			<div className="page-body">
				<PageTitle left="Campaigns" />
				<div className="br br-gray2 radius padding20 margin30">
					<div className="f20 gray2 bold marginHor20 textLeft">Campaigns Statuses</div>
					<div className="f16 gray2  textLeft marginBottom40">
						Enable / Disable campaigns. We will post your posts randomly
						<br /> Create your posts list that will bring the best traffix to your website.
						<br />
						<br />
						<span className="yellow f12">You can only 3 active campaigns.</span>
					</div>
				</div>

				{this.props.campaignsStore.campaigns.length && (
					<Select
						mode="multiple"
						className="paddingVer30"
						style={{ width: '100%' }}
						placeholder="Please select"
						value={this.props.campaignsStore.activeCampaigns.map((campaign: any) => `${campaign.id}`)}
						onSelect={(id: any) => this.props.campaignsStore.onCampaignChange(id, { active: 1 })}
						onDeselect={(id: any) => this.props.campaignsStore.onCampaignChange(id, { active: 0 })}
					>
						{this.props.campaignsStore.campaigns.map((campaign: any) => (
							<Option key={campaign.id}>{campaign.title}</Option>
						))}
					</Select>
				)}

				<div className="campaigns_wrap grid padding30 textLeft">
					{this.props.campaignsStore.activeCampaigns.map((campaign: any, i: number) => (
						<div className="box-traffix padding10 br br-gray2 radius ">
							<img className="full cover" height="200" src={campaign.featured_image} />
							<div className="gray2 f12 marginHor20 bold">{campaign.title}</div>
							<div className="gray2 f12 marginBottom40">{campaign.description}</div>

							<div className="flex full textCenter">
								<div className="gray2 flex1">
									<div className="bold flex">
										<ReactSVG className="icon-campaign-box" src="/images/icons/025-link-1.svg" />
										<div className="f12 gray2 margin5">
											{campaign.traffixs.length || 0}
											<span className="yellow f12"> Traffix In</span>
										</div>
									</div>
								</div>
							</div>
						</div>
					))}
				</div>
			</div>
		);
	}
}
export default withRouter(Campaigns);
