import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import "./StepsRoute.scss";
import Step1 from "./Step1/Step1";
import Step2 from "./Step2/Step2";
import Step3 from "./Step3/Step3";
import Step4 from "./Step4/Step4";

interface Props extends RouteComponentProps<any> {
  step: Number;
  handleUserData: (data: any) => void;
}

class StepsRoute extends React.PureComponent<Props, any> {
  renderStep(step: Number) {
    switch (step) {
      case 0:
        return <Step1 />;
        break;
      case 1:
        return <Step2 handleUserData={this.props.handleUserData} />;
        break;
      case 2:
        return <Step4 />;
        break;
    }
  }

  render() {
    let { step } = this.props;
    return <div>{this.renderStep(step)}</div>;
  }
}
export default withRouter(StepsRoute);
