import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import { Row, Col, Slider, Modal, Radio, Divider } from "antd";
import DashboardStore from "../../stores/DashboardStore";
import "./PageHeader.scss";

import { observable, computed } from "mobx";
import { observer, inject } from "mobx-react";
import { setTimeout } from "timers";
import PaypalModal from "./PaypalModal/PaypalModal";
import AuthStore from "../../stores/AuthStore";

let amount: number = 0;
let credit: number = 0;

declare global {
  interface Window {
    paypal: any;
  }
}

interface Props extends RouteComponentProps<any> {
  dashboardStore?: DashboardStore;
  authStore?: AuthStore;
}
@inject("dashboardStore", "authStore")
@observer
class PageHeader extends React.PureComponent<Props, any> {
  @observable website_credits: number = 0;
  @observable website_monthly_traffix: number = 0;
  @observable modalPaymentVisible: any = false;

  componentDidMount = async () => {
    try {
      let websiteCredits = await this.props?.dashboardStore?.getWebsiteCredits();
      this.website_credits = Number(websiteCredits.website_credits.credits);
 
    } catch (err) {
      console.error(err);
    }
  };

  showModalPayment = () => {
    this.modalPaymentVisible = true;
  };

  hideModalPayment = () => {
    this.modalPaymentVisible = false;
  };

  @computed get displayPaymentBar() {
    return (
      this.props.authStore?.isAuthenticated &&
      this.props.authStore.website?.is_enabled
    );
  }

  render() {
    return (
      <Row className="br-bottom  b-blue br-blue" style={{}}>
        <Col className="padding20 flex" span={2}>
          <img src="/images/logo.png" width="30"></img>
          <strong className="f20 white">OUR</strong>
          <strong className="f20 white">TRAFFIX</strong>
        </Col>
        {!this.displayPaymentBar ? null : (
          <Col className="textLeft padding10" span={22}>
            <div className="flex credit-container">
              {/* <div className="full marginTop10 marginRight20">
                <div className="full flow-root">
                  <div className="f5 gray fLeft marginRight20">Traffix in</div>
                  <div className="f5 gray fRight">
                    <b>{this.website_monthly_traffix}</b> /{" "}
                    {this.website_credits}
                  </div>
                </div>
                {this.website_credits > 0 && (
                  <Slider
                    className="no-margin"
                    value={this.website_monthly_traffix}
                    max={this.website_credits}
                    disabled={true}
                  />
                )}
              </div> */}

              <div className="white flex padding10 marginRight20">
                Credit
                <Divider
                  style={{ margin: "7px" }}
                  className="gray2"
                  type="vertical"
                />
                <b>{this.website_credits}</b>
              </div>

              <div
                onClick={() => this.showModalPayment()}
                className="red more_credit"
              >
                More Credits
              </div>
            </div>
          </Col>
        )}
        <Modal
          visible={this.modalPaymentVisible}
          onCancel={this.hideModalPayment}
          footer={null}
        >
          <PaypalModal />
        </Modal>
      </Row>
    );
  }
}
export default withRouter(PageHeader);
