import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import { Table, Tag } from "antd";
import "./Traffix.scss";
import Moment from "react-moment";
import { observer, inject } from "mobx-react";
import TraffixsStore from "../../stores/TraffixsStore";
import { action } from "mobx";
import PageTitle from "../../common/PageTitle/PageTitle";

interface Props extends RouteComponentProps<any> {
  traffixsStore: TraffixsStore;
}

const columns = [
  {
    title: "Refferal Url",
    dataIndex: "referral_url",
  },
  {
    title: "User Country",
    dataIndex: "user_country",
  },
  {
    title: "User City",
    dataIndex: "user_city",
  },
  {
    title: "Visiting Time",
    dataIndex: "visiting_time",
  },
  {
    title: "Campaign id",
    dataIndex: "campaign_id",
  },
  {
    title: "Traffix",
    dataIndex: "traffix",
  },
];

@inject("traffixsStore")
@observer
class Traffix extends React.PureComponent<Props, any> {
  @action componentDidMount = async () => {
    try {
      this.props.traffixsStore.fetchPage({});
    } catch (err) {}
  };

  onPageChange = (page: number, pageSize?: number | undefined) => {
    this.props.traffixsStore.fetchPage({ page, pageSize });
  };

  render() {
    const { currentPage } = this.props.traffixsStore;
    const data = currentPage.map((traffix: any, i: any) => ({
      key: i,
      referral_url: (
        <a className="" href={traffix.website.url} target="_blank">
          {traffix.website.url}
        </a>
      ),
      user_country: traffix.visitor_info?.country?.name,
      user_city: traffix.visitor_info?.city?.name,
      campaign_id: traffix.campaign_id,
      visiting_time: <Moment fromNow>{traffix.createdAt}</Moment>,
      traffix: traffix.in ? (
        <Tag className="textCenter" color="green">
          In
        </Tag>
      ) : (
        <Tag className="textCenter" color="red">
          Out
        </Tag>
      ),
    }));

    return (
      <div className="page-body">
        <PageTitle left="Traffix" />
        <div className="br br-gray2 radius padding20 margin30">
          <div className="f20 gray2 bold marginHor20 textLeft">Traffix Log</div>
          
          <Table
            columns={columns}
            dataSource={data}
            size="small"
            pagination={{
              showSizeChanger: true,
              showQuickJumper: true,
              total: this.props.traffixsStore.total,
              onChange: this.onPageChange,
              onShowSizeChange: this.onPageChange,
              current: Number(this.props.traffixsStore.currentPageNumber),
              pageSize: Number(this.props.traffixsStore.pageSize),
              pageSizeOptions: this.props.traffixsStore.pageSizeOptions,
            }}
          />
        </div>
      </div>
    );
  }
}
export default withRouter(Traffix);
