import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import PageHeader from "../../common/PageHeader/PageHeader";
import { message, Popconfirm } from "antd";
import { SketchPicker } from "react-color";
import { observable } from "mobx";
import { observer } from "mobx-react";
import "./Settings.scss";
import PageTitle from "../../common/PageTitle/PageTitle";

interface Props extends RouteComponentProps<any> {}

@observer
class Settings extends React.PureComponent<Props, any> {
  @observable color: any = "#fff";
  @observable displayColorPicker: Boolean = false;

  confirm() {
    message.info(
      "Feed disabled on your website , you can always  turn it on againg :)"
    );
  }

  handleChangeComplete = (color: any) => {
    this.color = color.hex;
  };

  handleClick = () => {
    this.displayColorPicker = !this.displayColorPicker;
  };

  handleClose = () => {
    this.displayColorPicker = false;
  };

  handleChange = (color: any) => {
    this.color = color.hex;
  };

  render() {
    return (
      <div className="">
        <PageTitle left="Settings" />
        <div className="padding20">
          <div className="flex marginTop10">
            <Popconfirm
              placement="top"
              title={
                "If you disable YourTraffix feed you will stop getting traffix from other websites"
              }
              onConfirm={this.confirm}
              okText="Yes"
              cancelText="No"
            >
              <div className="radius  red textCenter   pointer inline-block">
                Disable Feed
              </div>
            </Popconfirm>
          </div>
          <div className="flex marginTop10">
            <div className="">Feed Color</div>

            <div className="color-pick" onClick={() => this.handleClick()}>
              <div />
            </div>
            {this.displayColorPicker ? (
              <div>
                <div onClick={() => this.handleClose()} />
                <SketchPicker
                  color={this.color}
                  onChangeComplete={this.handleChangeComplete}
                />
              </div>
            ) : null}
          </div>
        </div>
      </div>
    );
  }
}
export default withRouter(Settings);
