import React from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { Row } from 'antd';
import './PageTitle.scss';
import { observer } from 'mobx-react';

interface Props extends RouteComponentProps<any> {
	right?: any;
	left?: any;
}

class PageTitle extends React.PureComponent<Props, any> {
	render() {
		let { right, left } = this.props;
		return (
			<Row className="header  paddingVer40   bold br-bottom  flex br-gray gray2">
				<div className="fright">{left}</div>
				<div className="range-picker-wrapper textLeft flex1">{right}</div>
			</Row>
		);
	}
}
export default withRouter(PageTitle);
