import axios, { CancelToken } from "axios";
import qs from "qs";
import { ICampaign } from "../pages/Intro/Steps/Step2/Step2";

export type ILogin = {
  url: string;
  email: string;
  user_info: any;
  site_info: any;
  campaigns: ICampaign[];
};

// http://local.wp.yourtraffix.com/
// http://api.yourtraffix.com/

class Api {
  BASE_URL =
    process.env.NODE_ENV === "production"
      ? window.location.protocol + "//" + window.location.host + "/"
      : "http://local.wp.yourtraffix.com/";

  WP_AJAX_URL = `${this.BASE_URL}wp-admin/admin-ajax.php`;

  API_URL = `${process.env.NODE_ENV === "production" ? "https" : "http"}://${
    process.env.NODE_ENV === "production" ? "" : "local."
  }api.yourtraffix.com`;

  wpPrepareDateRequest = (data: any) => {
    return qs.stringify(JSON.parse(JSON.stringify(data)));
  };

  login = async (user: any) => {
    return await axios.post(`${this.API_URL}/users/login`, user);
  };

  setWebsiteEnabled = async (website: any, campaigns: any) => {
    return await axios.post(
      `${this.API_URL}/websites/${website.id}/actions/enable`,
      campaigns
    );
  };

  register = async (user: any) => {
    return await axios.post(`${this.API_URL}/users/register`, user);
  };

  getUserData = async () => {
    try {
      const data = {
        action: "get_user_data",
      };

      const response = await axios.post(
        `${this.WP_AJAX_URL}`,
        this.wpPrepareDateRequest(data)
      );

      return response;
    } catch (err) {
      return err;
    }
  };

  getTraffixsByGroupInOut = async (startTime: number, endTime: number) => {
    try {
      const response = await axios.get(
        `${this.API_URL}/traffixs/groupInOut?startTime=${startTime}&endTime=${endTime}`
      );

      return response;
    } catch (err) {
      return err;
    }
  };

  getWebsiteCredits = async () => {
    try {
      const response = await axios.get(`${this.API_URL}/website-credits`);
      return response;
    } catch (err) {
      return err;
    }
  };

  getTraffixs = async (
    page: number,
    pageSize: number,
    startTime: number,
    endTime: number
  ) => {
    try {
      const response = await axios.get(
        `${this.API_URL}/traffixs/?page=${page}&pageSize=${pageSize}&startTime=${startTime}&endTime=${endTime}`
      );
      return response;
    } catch (err) {
      return err;
    }
  };

  fetchTraffixsByPage = async (
    offset: number = 0,
    limit: number = 6,
    startTime: number | undefined | string = "",
    endTime: number | undefined | string = ""
  ) => {
    try {
      const response = await axios.get(
        `${this.API_URL}/traffixs?offset=${offset}&limit=${limit}&startTime=${startTime}&endTime=${endTime}`,
        {
          withCredentials: true,
          // cancelToken: cancelToken ? cancelToken : undefined
        }
      );
      return response;
    } catch (err) {
      return err;
    }
  };

  updateCampaign = async (campaign: any) => {
    try {
      const response = await axios.put(
        `${this.API_URL}/campaigns/${campaign.id}`,
        campaign,
        {
          withCredentials: true,
        }
      );

      return response;
    } catch (err) {
      return err;
    }
  };

  fetchCampaigns = async () => {
    try {
      const response = await axios.get(`${this.API_URL}/campaigns`, {
        withCredentials: true,
      });

      return response;
    } catch (err) {
      return err;
    }
  };

  getCurrentUser = async () => {
    try {
      const response = await axios.get(`${this.API_URL}/users/me`, {
        withCredentials: true,
      });

      return response;
    } catch (err) {
      return err;
    }
  };
  fetchTotalCampaigns = async (
    startTime: number | string = "",
    endTime: number | string = ""
  ) => {
    try {
      const response = await axios.get(
        `${this.API_URL}/campaigns/total/?startTime=${startTime}&endTime=${endTime}`
      );
      return response;
    } catch (err) {
      return err;
    }
  };
}

export default Api;
