import React from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { Steps, Icon, Slider } from 'antd';
import './IntroSteps.scss';
import { observable } from 'mobx';

const { Step } = Steps;
interface Props extends RouteComponentProps<any> {
	currentStep: number;
}

class IntroSteps extends React.PureComponent<Props, any> {
	render() {
		return (
			<div className="intro_steps">
				<Steps current={this.props.currentStep} className="intro-steps">
					<Step key={1} title="Welcome" />
					<Step key={2} title="Enable Your Traffix" />
					<Step key={3} title="Start sharing" />
				</Steps>
			</div>
		);
	}
}
export default withRouter(IntroSteps);
