import React from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import { CampaignStyled } from './styled';
import { ICampaign } from '../../../pages/Intro/Steps/Step2/Step2';
import { Skeleton } from 'antd';

interface Props extends RouteComponentProps<any> {
	campaign: ICampaign;
}

class Campaign extends React.PureComponent<Props, any> {
	render() {
		const { campaign } = this.props;
		const component = campaign ? (
			<CampaignStyled>
				<div className="f12">{campaign.title}</div>
				<div className="date gray f11">{campaign.date}</div>
				<div className="image">
					<img src={campaign.featured_image} alt={campaign.title} />
				</div>
			</CampaignStyled>
		) : (
			<Skeleton active />
		);

		return component;
	}
}
export default withRouter(Campaign);
