import axios from "axios";
import { action, observable, reaction } from "mobx";
import Api from "../services/Api";
import CampaignsStore from "./CampaignsStore";
import TraffixsStore from "./TraffixsStore";
import moment from "moment";

class DashboardStore {
  api: Api;
  traffixsStore: TraffixsStore;
  campaignsStore: CampaignsStore;

  @observable website_credits: number = 0;
  @observable website_monthly_traffix: number = 0;
  @observable traffixByGroupInOut: any = [];
  @observable traffixSumIn: any = 0;
  @observable traffixSumOut: any = 0;
  @observable startTime: number = moment().subtract(1, "months").unix();
  @observable endTime: number = moment().unix();

  constructor(
    api: Api,
    campaignsStore: CampaignsStore,
    traffixsStore: TraffixsStore
  ) {
    this.api = api;
    this.campaignsStore = campaignsStore;
    this.traffixsStore = traffixsStore;

    reaction(
      () => ({
        startTime: this.startTime,
        endTime: this.endTime,
      }),
      () => this.updateData()
    );
  }

  updateData = () => {
    this.getTraffixsByGroupInOut(this.startTime, this.endTime);
    this.traffixsStore.currentPage = [];
    this.traffixsStore.fetchPage({
      startTime: this.startTime,
      endTime: this.endTime,
      page: 1,
      pageSize: 5,
    });
    // this.getTraffixs(this.this.startTime, this.this.endTime);
    this.campaignsStore.fetchTotal(this.startTime, this.endTime);
  };

  @action async getWebsiteCredits() {
    try {
      const response = await this.api.getWebsiteCredits();
      return response.data;
    } catch (err) {
      console.error(err);
    }
  }

  @action setRangeDates = (startTime: any, endTime: any) => {
    this.startTime = startTime;
    this.endTime = endTime;
  };

  @action async getTraffixsByGroupInOut(startTime: number, endTime: number) {
    try {
      const response = await this.api.getTraffixsByGroupInOut(
        startTime,
        endTime
      );

      this.traffixByGroupInOut = response.data?.traffixs || [];
      this.traffixSumIn = response.data?.sumIn || 0;
      this.traffixSumOut = response.data?.sumOut || 0;

      return response;
    } catch (err) {
      console.error(err);
    }
  }
}
// We instantiate the collection and export it as a singleton
export default DashboardStore;
