import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import { Radio } from "antd";
import { observable, action } from "mobx";
import { observer, inject } from "mobx-react";
import AuthStore from "../../../stores/AuthStore";

interface Props extends RouteComponentProps<any> {authStore?: AuthStore;}

let amount: number = 0;
let credit: number = 0;



@inject("authStore")
@observer
class PaypalModal extends React.PureComponent<Props, any> {
  @observable price: any = "FREE";
  @observable estimateWebsites: any = 3;
  @observable creditValue: number = 1;

  componentDidMount = async () => {
    const website_id = this.props.authStore?.website?.id;
    
    window.paypal.Button.render(
      {
        env: "production", //sandbox
        payment: function (data: any, actions: any) {
          return actions.request
            .post("https://api.yourtraffix.com/payment/create-payment", {
              amount: amount,
            })

            .then(function (res: any) {
              return res.id;
            });
        },

        onAuthorize: function (data: any, actions: any) {
          console.log(["data" , website_id])
          return actions.request
            .post("https://api.yourtraffix.com/payment/execute-payment/", {
              amount: amount,
              credit: credit,
              paymentID: data.paymentID,
              payerID: data.payerID,
              websiteId: website_id
            })
            .then(function (res: any) {});
        },
      },
      "#paypal-button"
    );
  };

  onChange = (e: any) => {
    this.creditValue = e.target.value;

    switch (Number(e.target.value)) {
      case 1:
        this.price = "FREE";
        this.estimateWebsites = 3;
        amount = 0;
        break;
      case 2:
        this.price = "100$";
        this.estimateWebsites = 10;
        amount = 100;
        credit = 50;
        break;
      case 3:
        this.price = "180$";
        this.estimateWebsites = 20;
        amount = 180;
        credit = 100;
        break;
      case 4:
        this.price = "250$";
        this.estimateWebsites = 50;
        amount = 250;
        credit = 200;
        break;
      case 5:
        this.price = "380$";
        this.estimateWebsites = 100;
        amount = 380;
        credit = 500;
        break;
      case 6:
        this.price = "480$";
        this.estimateWebsites = 200;
        amount = 480;
        credit = 1000;
        break;
    }
  };

  render() {
    return (
      <>
        <div className="">
          Credit = <b> 50 Cent</b> (for any incoming traffix )
        </div>
        <Radio.Group
          className="grid marginHor20"
          onChange={(e) => this.onChange(e)}
          value={this.creditValue}
        >
          <Radio value={1}>Free Package (current package)</Radio>
          <Radio value={2}>50 Credits</Radio>
          <Radio value={3}>100 Credits</Radio>
          <Radio value={4}>200 Credits</Radio>
          <Radio value={5}>500 Credits</Radio>
          <Radio value={6}>1000 Credits</Radio>
        </Radio.Group>
        <div className="marginHor20">
          <div className="bold">{this.price}</div>
          <div className="">* estimate websites you will apper on</div>
          <div className="red">{this.estimateWebsites} websites</div>
        </div>

        <div className="f6">
          <div className="marginTop20">
            <Radio value={7} checked={true}>
              <img src="/images/paypal.png" width="100" />
            </Radio>
          </div>
         
        </div>

        <div id={"paypal-button"}></div>
      </>
    );
  }
}
export default withRouter(PaypalModal);
