import React from "react";
import {
  withRouter,
  RouteComponentProps,
  Switch,
  Redirect,
  Route,
} from "react-router-dom";
import Dashboard from "./Dashboard/Dashboard";
import Traffix from "./Traffix/Traffix";
import Settings from "./Settings/Settings";
import Intro from "./Intro/Intro";
import Campaigns from "./Campaigns/Campaigns";
import AuthStore from "../stores/AuthStore";
import { inject, observer } from "mobx-react";
import PrivateRoute from "./PrivateRoute";
import PublicOnlyRoute from "./PublicOnlyRoute";
import Auth from "./Auth/Auth";
import Register from "./Auth/Register/Register";
import Login from "./Auth/Login/Login";
import ResetPass from "./Auth/ResetPass/ResetPass";
import ForgotPass from "./Auth/ForgotPass/ForgotPass";

interface Props extends RouteComponentProps<any> {
  authStore?: AuthStore;
}

@inject("authStore")
@observer
class AppRouter extends React.PureComponent<Props, any> {
  render() {
    return (
      <Switch>
        <PrivateRoute path={"/dashboard"} component={Dashboard} />
        <PrivateRoute path={"/traffix"} component={Traffix} />
        <PrivateRoute path={"/settings"} component={Settings} />
        <PrivateRoute path={"/campaigns"} component={Campaigns} />
        <PrivateRoute path={"/intro"} component={Intro} />

        <Route
          path="/users"
          render={({ match: { url } }) => (
            <>
              <PublicOnlyRoute path={`${url}/`} component={Auth} />
              <PublicOnlyRoute path={`${url}/login`} component={Login} />
              <PublicOnlyRoute path={`${url}/register`} component={Register} />
              <PublicOnlyRoute
                path={`${url}/reset-password`}
                component={ResetPass}
              />
              <PublicOnlyRoute
                path={`${url}/forgot-password`}
                component={ForgotPass}
              />
            </>
          )}
        />

        <PrivateRoute path={"/"} component={Dashboard} />
      </Switch>
    );
  }
}
export default withRouter(AppRouter);
