import React from "react";
import { withRouter, RouteComponentProps, Link } from "react-router-dom";
import { Menu, Avatar, Row, Col, Divider } from "antd";
import { ReactSVG } from "react-svg";
import { observable, computed } from "mobx";
import { observer, inject } from "mobx-react";
import "./LeftMenu.scss";
import AuthStore from "../../stores/AuthStore";
import md5 from "md5";

const { SubMenu } = Menu;

interface Props extends RouteComponentProps<any> {
  authStore?: AuthStore;
}

@inject("authStore")
@observer
class LeftMenu extends React.PureComponent<Props, any> {
  @observable path: string = "";

  componentDidMount() {
    let url = this.props.history.location.pathname;
    console.log(url);
    this.path = url;
  }

  @computed get displayLeftMenu() {
    return (
      this.props.authStore?.isAuthenticated &&
      this.props.authStore?.user?.website?.is_enabled
    );
  }

  render() {
    const { authStore } = this.props;
    return !this.displayLeftMenu ? null : (
      <div className="left_manu">
        <div className="LeftMenu full-height br-right br-gray ">
          <div className="user_info">
            <Row className="user_info_wrapper flex ">
              <Col span={6}>
                <Avatar
                  size={46}
                  // src="https://d19m59y37dris4.cloudfront.net/dark-admin-premium/1-4-5/img/avatar-6.jpg"
                  src={`https://s.gravatar.com/avatar/${md5(
                    authStore!.user?.email || ""
                  )}?s=80`}
                />
              </Col>
              <Col className="textLeft  flex itemCenter" span={18}>
                {/* <div className="f4 gray2 bold">Nati Giat</div> */}
                <div className="f4 gray bold user-email">
                  {authStore!.user?.email}
                </div>
              </Col>
            </Row>
          </div>
          <div className="textLeft gray2 paddingVer20 f20 grayTitle">Main</div>
          <div className="grid menu-left-wrapper">
            <Link
              to={"dashboard"}
              className="f18 capitalize gray padding10 paddingVer20 textLeft flow-root   c-vertical"
            >
              <ReactSVG
                className="icon-menu-box"
                src="/images/icons/home.svg"
              />
              <Divider type="vertical" className="b-gray2"></Divider>
              dashboard
            </Link>

            <Link
              to={"traffix"}
              className="f18 capitalize gray padding10 paddingVer20 textLeft flow-root  c-vertical"
            >
              <ReactSVG
                className="icon-menu-box"
                src="/images/icons/link.svg"
              />
              <Divider type="vertical" className="b-gray2"></Divider>
              traffix
            </Link>

            <Link
              to={"campaigns"}
              className="f18 capitalize gray padding10 paddingVer20 textLeft flow-root  c-vertical"
            >
              <ReactSVG
                className="icon-menu-box"
                src="/images/icons/speedometer.svg"
              />
              <Divider type="vertical" className="b-gray2"></Divider>
              Boosted Posts
            </Link>

            {/* <Link
            to={"settings"}
            className="f18 capitalize gray padding10 paddingVer20 textLeft flow-root hoverRed c-vertical"
          >
            <ReactSVG
              className="icon-menu-box"
              src="/images/icons/settings.svg"
            />
            <Divider type="vertical" className="b-gray2"></Divider>
            settings
          </Link> */}
          </div>
        </div>
      </div>
    );
  }
}
export default withRouter(LeftMenu);
