import Api from "../services/Api";
import PageStore from "./PageStore";

class TraffixsStore extends PageStore {
  api: Api;

  constructor(api: Api) {
    super();
    this.api = api;
  }

  _fetchPage = async (
    offset: number,
    limit: number,
    startTime?: number | undefined,
    endTime?: number | undefined
  ) => {
    return await this.api.fetchTraffixsByPage(
      offset,
      limit,
      startTime,
      endTime
    );
  };
}

// We instantiate the collection and export it as a singleton
export default TraffixsStore;
