import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";

import {
  ResponsiveContainer,
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  Legend,
} from "recharts";

import "./TraffixChart.scss";
import { observer, inject } from "mobx-react";
import { observable, toJS } from "mobx";
import Api from "../../../../services/Api";
import DashboardStore from "../../../../stores/DashboardStore";

interface Props extends RouteComponentProps<any> {
  api?: Api;
  dashboardStore?: DashboardStore;
}
@inject("api", "dashboardStore")
@observer
class TraffixChart extends React.PureComponent<Props, any> {
  render() {
    return (
      <ResponsiveContainer height="100%" width="100%">
        <LineChart
          data={toJS(this.props?.dashboardStore?.traffixByGroupInOut)}
          margin={{ top: 0, left: 0, right: 0, bottom: 0 }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="date" />
          <YAxis />
          <Tooltip />
          <Legend />
          <Line type="monotone" dataKey="in" stroke="#fbc108" />
          <Line type="monotone" dataKey="out" stroke="#4351b2" />
        </LineChart>
      </ResponsiveContainer>
    );
  }
}
export default withRouter(TraffixChart);
