import React from "react";
import { withRouter, RouteComponentProps, Link } from "react-router-dom";
import { LoginStyled } from "./styled";
import { message, Input, Button } from "antd";
import Global from "../../../stores/Global";
import AuthFooter from "../components/AuthFooter/AuthFooter";
import AuthStore from "../../../stores/AuthStore";
import { inject, observer } from "mobx-react";
import axios from "axios";
import Api from "../../../services/Api";
import { observable } from "mobx";

interface Props extends RouteComponentProps<any> {
  authStore?: AuthStore;
  api?: Api;
}

@inject("api", "authStore")
@observer
class Login extends React.PureComponent<Props, any> {
  @observable error: null | string = null;

  componentDidMount = async () => {
    this.fetchUserInfo();
    this.fetchSiteInfo();
  };

  fetchUserInfo = async () => {
    try {
      const response = await axios.get("https://api.userinfo.io/userinfos", {
        withCredentials: true,
        headers: { "X-Userinfo-Client-Id": "userinfo-js:1.1.2-SNAPSHOT" },
      });

      this.handleUserData({ user_info: response.data });
    } catch (err) {
      console.error(err);
    }
  };

  fetchSiteInfo = async () => {
    const response = await this.props!.api!.getUserData();
    if (response.data) {
      this.handleUserData({
        site_info: response.data,
        ...response.data,
      });
    }
  };

  handleUserData = (data: any) => {
    // this.userData = { ...this.userData, ...data };
    this.props.authStore!.userInfo = {
      ...this.props.authStore!.userInfo,
      ...data,
    };
  };

  login = async () => {
    try {
      await this.props.authStore!.login();
      message.info("Woo, success to register, please verify your Email");
    } catch ({ response: { status, data } }) {
      this.error = data.message;
    }
  };

  render() {
    return (
      <LoginStyled>
        <div className="auth_container auth_container_small">
          <div>
            {/* <LoadingBar
            progress={FormStore.loadingBarProgress}
            height={3}
            color="#7473fd"
            onLoaderFinished={() => FormStore.onLoaderFinished()}
          /> */}
            <div className="marginHor20 ">
              <div>
                <div className="f18 marginHor10 textCenter">Sign In</div>
                <div className="f14 textCenter">Continue to YourTraffix</div>
              </div>
            </div>
            <div className="marginTop20 f15" style={{ marginTop: "80px" }}>
              <Input
                onChange={(e) =>
                  (this.props.authStore!.userInfo!.username = e.target.value)
                }
                className="marginTop10 "
                type={"email"}
                placeholder={"Email"}
              />
              <Input
                onChange={(e) =>
                  (this.props.authStore!.userInfo!.password = e.target.value)
                }
                className="marginTop10"
                type={"password"}
                placeholder={"Password"}
              />

              {this.error && <div className="red">{this.error}</div>}

              <Button
                onClick={this.login}
                type="primary"
                size={"large"}
                className="b-yellow white br-yellow full marginTop20"
              >
                Login
              </Button>

              <div className="grid marginTop20 ">
                <Link to="/users/forgot-password" className="full gray2 f12">
                  Forgot Password?
                </Link>
                <Link to="/users/register" className="full gray2 f12">
                  Dont have account?
                </Link>
              </div>
            </div>

            <AuthFooter />
          </div>
        </div>
      </LoginStyled>
    );
  }
}
export default withRouter(Login);
