import React from 'react';
import { withRouter, RouteComponentProps } from 'react-router-dom';
import './Step2.scss';
import FeedBar from '../../../../common/FeedBar/FeedBar';
import { observable, action, computed } from 'mobx';
import { observer } from 'mobx-react';
import { Select } from 'antd';
import qs from 'qs';
import axios from 'axios';
import { message, Button } from 'antd';

const { Option } = Select;

export interface ICampaign {
	ID: string;
	url: string;
	title: string;
	description: string;
	featured_image: string;
	date: string;
}

interface Props extends RouteComponentProps<any> {
	handleUserData: (data: any) => void;
}
@observer
class Step2 extends React.PureComponent<Props, any> {
	BASE_URL = process.env.NODE_ENV === 'production'
		? window.location.protocol + '//' + window.location.host + '/'
		: 'http://local.wp.yourtraffix.com/';
	WP_AJAX_URL = `${this.BASE_URL}wp-admin/admin-ajax.php`;

	user: any = {};

	@observable active: Boolean = false;
	@observable selectedRowKeys: any[] = [];
	@observable posts: any[] = [];
	@observable fetchPostsLoading: boolean = false;
	// @observable campaigns: ICampaign[] = [];

	@action
	onSelectChange = (selectedRowKeys: any) => {
		if (selectedRowKeys.length > 3) {
			message.warning('You can only choose 3 posts.');
			return;
		}
		this.selectedRowKeys = selectedRowKeys;
		this.props.handleUserData({ campaigns: this.campaigns });
	};

	@computed
	get campaigns() {
		return this.posts
			.filter((post: any, index: number) => this.selectedRowKeys.includes(`${post.ID}`))
			.map((post: any): ICampaign => ({
				ID: post.ID,
				url: post.guid,
				title: post.post_title,
				description: post.post_excerpt,
				date: post.post_modified,
				featured_image:
					post.featured_image || 'https://dummyimage.com/600x400/ffffff/4552b3&text=Missing+featured+image'
			}));
	}

	wpPrepareDateRequest = (data: any) => {
		return qs.stringify(JSON.parse(JSON.stringify(data)));
	};

	componentDidMount = async () => {
		try {
			const data = {
				action: 'get_posts'
			};
			this.fetchPostsLoading = true;
			const response = await axios.post(this.WP_AJAX_URL, this.wpPrepareDateRequest(data));

			this.fetchPostsLoading = false;
			this.posts = response.data.posts;
		} catch (err) {
			console.error(err);
		}
	};
	render() {
		return (
			<div>
				<div className=" marginCenter " style={{ maxWidth: '750px' }}>
					<div className="marginTop60 bold f20">Boost your posts</div>

					<div className="f15 marginHor20 gray" style={{ maxWidth: '550px' }}>
						A websites content is arguably the most important factor influencing conversion rates. It also
						plays an integral role in how your individual pages perform in organic search. Great marketing
						content has the ability to seamlessly blend conversion opportunities.
						<br />
						<br />
						Please choose 3 post that you want to share
					</div>

					<Select
						mode="multiple"
						className="marginBottom20"
						style={{ width: '100%' }}
						placeholder="Choose your campaigns"
						value={this.campaigns.map((campaign: any) => `${campaign.ID}`)}
						onChange={this.onSelectChange}
					>
						{this.posts.map((campaign: any) => <Option key={campaign.ID}>{campaign.post_title}</Option>)}
					</Select>
					<FeedBar campaigns={this.campaigns.slice(0, 3)} />
				</div>
			</div>
		);
	}
}
export default withRouter(Step2);
