import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import { Row, Col } from "antd";
import TraffixChart from "./components/TraffixChart/TraffixChart";
import TraffixsStore from "../../stores/TraffixsStore";
import { observer, inject } from "mobx-react";
import { ReactSVG } from "react-svg";
import "./Dashboard.scss";
import DashboardStore from "../../stores/DashboardStore";
import moment from "moment";

import PageTitle from "../../common/PageTitle/PageTitle";
import RangePickerDashboard from "./components/RangePickerDashboard/RangePickerDashboard";
import CampaignsStore from "../../stores/CampaignsStore";
import TraffixMap from "./components/TraffixMap/TraffixMap";
import Flags from "../../common/Flags/Flags";

interface Props extends RouteComponentProps<any> {
  traffixsStore: TraffixsStore;
  dashboardStore: DashboardStore;
  campaignsStore: CampaignsStore;
}

@inject("traffixsStore", "dashboardStore", "campaignsStore")
@observer
class Dashboard extends React.PureComponent<Props, any> {
  componentDidMount = () => {
    this.props?.dashboardStore?.updateData();
  };
  render() {
    const { dashboardStore, campaignsStore, traffixsStore } = this.props;
    return (
      <div className="Dashboard page-body">
        <PageTitle left="Dashboard" right={<RangePickerDashboard />} />
        <div className="padding30">
          <div className="grid g-3fr-row g-column-gap-20">
            <div className="box">
              <div className="br br-gray2 radius  padding20 traffix-sum">
                <span className="textLeft gray">Traffix In</span>
                <span className="yellow f50">
                  {dashboardStore.traffixSumIn}
                </span>
              </div>
            </div>

            <div className="box">
              <div className="br br-gray2 radius  padding20 traffix-sum">
                <span className="gray textLeft">Campaigns</span>
                <span className="blue f50">{campaignsStore.total}</span>
              </div>
            </div>

            <div className="box">
              <div className="br br-gray2 radius  padding20 traffix-sum">
                <span className="textLeft gray">Traffix Out</span>
                <span className="blue f50">{dashboardStore.traffixSumOut}</span>
              </div>
            </div>
          </div>

          <div className="marginTop30 grid g-3fr-row g-column-gap-20 g-1-3-columns">
            <div
              className={`${
                traffixsStore.currentPage.length === 0 ? "flex" : "grid"
              } g-row-5fr   g-row-gap-20`}
            >
              {traffixsStore.currentPage.length === 0 ? (
                <div className="padding20  flex full br br-gray2 radius textCenter flex1 campaigns">
                  You don't have traffix yet.
                </div>
              ) : (
                traffixsStore.currentPage.map((traffix: any, i: number) => (
                  <div className="box-traffix br br-gray2 radius padding20 ">
                    <div className="gray bold marginRight10">
                      {moment(new Date(traffix.updatedAt)).format(
                        "MMMM d, YYYY"
                      )}
                    </div>

                    <div className="gray marginRight10">
                      <Flags
                        countryCode={traffix?.visitor_info?.country?.code}
                        style={{ margin: "0 8px 8px 0" }}
                      />
                      <span className="f12 textLeft bold gray2">
                        {traffix?.visitor_info?.country?.name}
                      </span>
                      <div className="f12 textLeft gray2">
                        {traffix?.visitor_info?.city?.name}
                      </div>
                    </div>

                    <div className="textCenter marginTop5 ">
                      <span className={traffix.in ? "yellow" : "blue"}>
                        {traffix.in ? "In" : "Out"}
                      </span>
                    </div>
                  </div>
                ))
              )}
            </div>

            <div className="traffix-chart br br-gray2 radius">
              <div
                className="chart-yt  padding20"
                style={{
                  height: "100%",
                  minHeight: "350px",
                  color: "white",
                }}
              >
                <TraffixChart />
              </div>
            </div>
          </div>

          <div className="marginTop30  b-gray" style={{ height: "550px" }}>
            <TraffixMap traffix={this.props?.traffixsStore?.currentPage} />
          </div>
        </div>
      </div>
    );
  }
}
export default withRouter(Dashboard);
