import React from "react";
import { withRouter, RouteComponentProps } from "react-router-dom";
import { RangePickerDashboardStyled } from "./styled";
import { DatePicker } from "antd";
import { observer, inject } from "mobx-react";
import { RangePickerValue } from "antd/lib/date-picker/interface";
import DashboardStore from "../../../../stores/DashboardStore";
import moment from "moment";

const { RangePicker } = DatePicker;

interface Props extends RouteComponentProps<any> {
  dashboardStore?: DashboardStore;
}

@inject("dashboardStore")
@observer
class RangePickerDashboard extends React.PureComponent<Props, any> {
  onCalendarChange = async ([startTime, endTime]: RangePickerValue) => {
    const _startTime: number = startTime?.unix() || new Date().getTime() / 1000;
    const _endTime: number = endTime?.unix() || new Date().getTime() / 1000;

    this.props?.dashboardStore?.setRangeDates(_startTime, _endTime);
  };

  render() {
    return (
      <RangePickerDashboardStyled>
        <RangePicker
          size="large"
          onCalendarChange={this.onCalendarChange}
          defaultValue={[
            moment((this.props?.dashboardStore?.startTime || 0) * 1000),
            moment((this.props?.dashboardStore?.endTime || 0) * 1000)
          ]}
          format={"DD/MM/YYYY"}
        />
      </RangePickerDashboardStyled>
    );
  }
}
export default withRouter(RangePickerDashboard);
